import React from "react";
import Login from "./Login";
import api from "../services/api";

const App = () => {
  const handlesignIn = (data) => {
    api.sendLogin(data).then((data) => {
      console.log(data);
    });
  };
  const renderLogin = () => {
    return (
      <>
        <Login handlesignIn={handlesignIn}></Login>
      </>
    );
  };
  return renderLogin();
};

export default App;
